import Vue from "vue";
import dropdowncity from "@/components/dropdowncity";
import "./index.css";
import httputils from "@/js/httputils";

new Vue({
  components: {
    dropdowncity
  },
  el: "#app",
  data() {
    let that = this;
    return {
      uploadUrl: "/travel/supergoproduct/NoticeManage/GetUrl?type=2",
      ruleForm: {
        PMId: $("#txtId").val(),
        PMBelong: "",
        PMType: "",
        PMNType: "",
        PMBeginDate: new Date().Format("yyyy-MM-dd"),
        PMEndDate: "",
        PMName: "",
        PMNameId: "",
        PMAgentType: 0,
        PMContent: "",
        Tag: [],
        City: [],
        Image: [],
        TagSelected: [1, 2, 3, 4, 5, 6],
        FileSelected: []
      },
      iteminfo: [{
        id: 1,
        name: ""
      },
      {
        id: 2,
        name: ""
      },
      {
        id: 3,
        name: ""
      },
      {
        id: 4,
        name: ""
      },
      {
        id: 5,
        name: ""
      },
      {
        id: 6,
        name: ""
      },
      {
        id: 7,
        name: ""
      },
      {
        id: 8,
        name: ""
      },
      {
        id: 9,
        name: ""
      },
      {
        id: 10,
        name: ""
      }
      ],
      taginfo: [{
        id: 1,
        name: ""
      },
      {
        id: 2,
        name: ""
      },
      {
        id: 3,
        name: ""
      },
      {
        id: 4,
        name: ""
      },
      {
        id: 5,
        name: ""
      },
      {
        id: 6,
        name: ""
      }
      ],
      dialogVisible: false,
      dialogImageUrl: "",
      rules: {
        PMBelong: [{
          required: true,
          message: "",
          trigger: "change"
        }],
        PMType: [{
          required: true,
          message: "",
          trigger: "blur"
        }],
        PMBeginDate: [{
          required: true,
          message: "",
          trigger: "blur"
        },
        {
          validator: (rule, value, callback) => {
            if (value === "") {
              callback(new Error(""));
            } else {
              if (that.ruleForm.PMEndDate) {
                if (that.ruleForm.PMEndDate < value) {
                  callback(new Error(""));
                }
              }
              callback();
            }
          },
          trigger: "blur"
        }
        ],
        PMEndDate: [{
          required: true,
          message: "",
          trigger: "blur"
        },
        {
          validator: (rule, value, callback) => {
            if (value === "") {
              callback(new Error(""));
            } else {
              if (that.ruleForm.PMBeginDate) {
                if (that.ruleForm.PMBeginDate > value) {
                  callback(new Error(""));
                }
              }
              callback();
            }
          },
          trigger: "blur"
        }
        ]
      }
    };
  },
  mixins: [{
    methods: minux
  }],

  mounted() {
    this.dataInit();
  },
  methods: {
    belongChange(val) {
      let that = this;
      that.ruleForm.FileSelected = [];
    },
    pmtypeChange(val) {
      let that = this;
      that.ruleForm.FileSelected = [];
      //
      if (val == 2) {
        that.ruleForm.PMNType = 1;
        that.uploadUrl = "/travel/supergoproduct/common/UploadImage";
      } else {
        that.uploadUrl = "/travel/supergoproduct/NoticeManage/GetUrl?type=2";
      }
    },
    pmntypeChange(val) {
      let that = this;
      that.ruleForm.FileSelected = [];
    },
    dataInit() {
      var that = this;
      if ($("#hidType").val() == "add") {
        return;
      }
      let loading = that.$loading({
        lock: true
      });
      httputils
        .get("/travel/supergoproduct/Material/GetDetail", {
          Id: $("#txtId").val()
        })
        .then(resp => {
          loading && loading.close();
          if (resp.IsSuccess) {
            that.ruleForm.PMBelong = resp.Result.PMBelong;
            that.ruleForm.PMType = resp.Result.PMType;
            that.ruleForm.PMNType = resp.Result.PMNType;
            that.ruleForm.PMBeginDate = resp.Result.PMBeginDate;
            that.ruleForm.PMEndDate = resp.Result.PMEndDate;
            that.ruleForm.PMNameId = resp.Result.PMNameId;
            that.ruleForm.PMAgentType = resp.Result.PMAgentType;
            that.ruleForm.PMContent = resp.Result.PMContent;
            that.ruleForm.City = resp.Result.City;
            var images = [];
            var tag = [];
            $(resp.Result.Tag).each(function () {
              tag.push(this.PTTagId);
            });
            $.each(resp.Result.Image, function () {
              images.push({
                name: this.PIImageName,
                url: this.PIImageUrl,
                productid: this.PIProductId
              });
            });
            that.ruleForm.FileSelected = images;
            that.ruleForm.TagSelected = tag;
            if (that.ruleForm.PMNameId == 0) {
              that.ruleForm.PMNameId = "";
            }
            that.uploadUrl = that.ruleForm.PMType == 2 ? "/travel/supergoproduct/common/UploadImage" : "/travel/supergoproduct/NoticeManage/GetUrl?type=2";
          }
        })
        .catch(err => {
          that.$message({
            type: "error",
            message: err
          });
          loading && loading.close();
        });
    },
    /**
     * 
     * @param {*} response
     * @param {*} file
     * @param {*} filelist
     */
    uploadsuccess(response, file, filelist) {
      var that = this;
      if (!that.ruleForm.FileSelected) {
        that.ruleForm.FileSelected = [];
      }
      if (that.ruleForm.PMType == 2) {
        if (response.Result && response.Result.length >= 1) {
          if (that.ruleForm.FileSelected.length < 9) {
            that.ruleForm.FileSelected.push({
              name: file.name,
              url: response.Result[0],
              productid: ""
            });
          }
        }
      } else {
        if (response.data && response.data.src && response.data.src.length >= 1) {
          if (that.ruleForm.FileSelected.length < 9) {
            that.ruleForm.FileSelected.push({
              name: file.name,
              url: response.data.src,
              productid: ""
            });
          }
        }
      }
    },
    /**
     * 
     * @param {*} response
     * @param {*} file
     * @param {*} filelist
     */
    attachmentSuccess(response, file, filelist) {
      var that = this;
      if (!that.ruleForm.FileSelected) {
        that.ruleForm.FileSelected = [];
      }
      if (response.data && response.data.src && response.data.src.length >= 1) {
        if (that.ruleForm.FileSelected.length < 9) {
          that.ruleForm.FileSelected.push({
            name: file.name,
            url: response.data.src,
            productid: ""
          });
        }
      }
    },
    /**
     * 
     * @param {*} file
     * @param {*} fileList
     */
    attachmentRemove(file, fileList) {
      let that = this;
      let att = that.ruleForm.FileSelected.filter(function (item) {
        return item.name == file.name;
      });
      if (att && att.length >= 1) {
        that.ruleForm.FileSelected.remove(att[0]);
      }
    },
    /**
     * 
     */
    onremove(index) {
      this.ruleForm.FileSelected.splice(index, 1);
    },
    /**
     * 
     */
    onpreview(obj) {
      this.dialogVisible = true;
      this.dialogImageUrl = obj;
    },
    /**
     * 
     * @param {*} file
     */
    attachmentPreview(file) {
      window.open(file.url);
    },
    submitForm: function (formName) {
      var that = this;
      this.$refs[formName].validate(valid => {
        if (valid) {
          let pmname = that.iteminfo.filter(function (item) {
            return item.id == that.ruleForm.PMNameId;
          });
          if (pmname && pmname.length >= 1) {
            that.ruleForm.PMName = pmname[0].name;
          }
          $(that.ruleForm.TagSelected).each(function (i, n) {
            let tag = that.taginfo.filter(function (e) {
              return e.id == n;
            });
            if (tag && tag.length >= 1) {
              that.ruleForm.Tag.push({
                PTTagId: n,
                PTName: tag[0].name
              });
            }
          });
          var image = [];
          $(that.ruleForm.FileSelected).each(function (i) {
            image.push({
              PIProductId: this.productid,
              PIImageName: this.name,
              PIImageUrl: this.url,
              PIImageType: that.ruleForm.PMNType == 3 ? 1 : 0,
              PIIhumbnailImageUrl: this.url
            });
          });
          that.ruleForm.Image = image;

          let loading = that.$loading({
            lock: true
          });
          httputils
            .post("/travel/supergoproduct/Material/Save", that.ruleForm)
            .then(result => {
              loading && loading.close();
              if (!result.IsSuccess) {
                that.$confirm(result.ErrorMsg, "", {
                  confirmButtonText: "",
                  type: "warning",
                  center: true,
                  showCancelButton: false
                });
              } else {
                that.$confirm("", "", {
                  confirmButtonText: "",
                  type: "success",
                  center: true,
                  showCancelButton: false
                }).then(() => {
                  if ($("#hidType").val() == "add") {
                    setTimeout(function () {
                      window.location.href = "/travel/supergoproduct/Material/EditMaterial?type=edit&id=" + result.Result;
                    }, 500);
                  }
                });
              }
            })
            .catch(err => {
              that.$message({
                type: "error",
                message: err
              });
              loading && loading.close();
            });
        } else {
          that.$confirm("", "", {
            confirmButtonText: "",
            type: "warning",
            center: true,
            showCancelButton: false
          });
          return;
        }
      });
    }
  }
});



